import http from '../../utils/http';
import { loading } from '../../utils/general';
import { contact, domestic, international } from '../../types/contact';
import { AsyncAppAction, RootState } from '..';
import { updateContactLoadingState, updateContacts, updateSelectedContact } from '../actionCreators/contact';

/**
 * create contact
 * @param payload
 * @returns
 */
export const createContact = (payload: {
    accountId: string,
    name: string,
    email?: string,
    phone?: string,
    card?: {
      address?: {
        addressType: 'card',
        line1: string,
        line2: string,
        city: string,
        state: string,
        country: string,
        postalCode: string
      }
    }
    intrabank?: {
      accountNumber: string;
    }
  }): AsyncAppAction<contact> => () => new Promise((resolve, reject) => {
  /* */
  loading(true);
  http
    .post<contact>('v1/contact', payload)
    .then(({ data }) => {
      resolve(data);
    })
    .catch(reject)
    .finally(() => {
      loading(false);
    });
});

/**
 * update contact
 * @param payload
 * @returns
 */
export const updateContact = (contactId: string, payload: {
  accountId: string,
  name?: string,
  email?: string,
  phone?: string,
  card?: {
    address?: {
      addressType: 'card',
      line1: string,
      line2: string,
      city: string,
      state: string,
      country: string,
      postalCode: string
    }
  },
  check?: {
    address?: {
      addressType: string,
      line1: string,
      line2: string,
      city: string,
      state: string,
      country: string,
      postalCode: string
    }
  },
  intrabank?: {
    accountNumber: string;
  }
  ach?: {
    accountNumber: string;
    routingNumber: string;
    accountType: string;
    bankName: string;
  },
  wire?: {
    domestic?: domestic;
    international?: international;
  }

}): AsyncAppAction<contact> => (dispatch) => new Promise((resolve, reject) => {
/* */
  loading(true);
  http
    .patch<contact>(`v1/contact/${contactId}`, payload)
    .then(({ data }) => {
      dispatch(updateSelectedContact(data));
      resolve(data);
    })
    .catch(reject)
    .finally(() => {
      loading(false);
    });
});

/**
 * delete connected bank account / cards
 * @param {string} contactId
 * @returns {*}
 */
export const deleteContact = (contactId: string) => () => new Promise((resolve, reject) => {
  loading(true);
  http
    .delete(`v1/contact/${contactId}`)
    .then(({ data }) => {
      resolve(data);
    })
    .catch((e) => {
      reject(e);
    })
    .finally(() => {
      loading(false);
    });
});

/**
   * @param contactId
   * @returns
   */
export const getLinkCardToken = (contactId: string): AsyncAppAction<{debitCardToken: string}> => () => new Promise((resolve, reject) => {
  loading(true);
  http(`v1/contact/${contactId}/debitcard/token`, {
    method: 'POST',
  })
    .then(({ data }) => {
      resolve(data);
    })
    .catch(reject)
    .finally(() => {
      loading(false);
    });
});

/**
 * @param {string} accountId
 * @returns
 */
export const getAllContacts = (accountId: string, limit = 25, offset = 0, query : string | null = null, type: string = 'others', otherFilters?: {[key: string]: string}): AsyncAppAction<{data: contact[], total: number}> => (dispatch, getState: () => RootState) => new Promise((resolve, reject) => {
  let url = `v1/contact?accountId=${accountId}&type=${type}&limit=${limit}&offset=${offset}`;
  if (query) {
    url = `${url}&query=${query}`;
  }
  if (otherFilters) {
    Object.keys(otherFilters).forEach((k) => {
      url += `&${k}=${otherFilters[k]}`;
    });
  }
  dispatch(updateContactLoadingState(true));
  http
    .get<{data: contact[], total: number}>(url)
    .then(({ data }) => {
      let d = data;
      if (offset !== 0) {
        const state = getState();
        const contacts = { ...state.contact.contacts };
        d = {
          data: [...contacts.data, ...data.data],
          total: data.total
        };
      }
      dispatch(updateContacts(d));
      resolve(d);
    })
    .catch(reject)
    .finally(() => {
      dispatch(updateContactLoadingState(false));
    });
});

/**
 * get contact by id
 */
export const getContactById = (contactId: string): AsyncAppAction<contact> => (dispatch) => new Promise((resolve, reject) => {
/* */
  loading(true);
  http
    .get<contact>(`v1/contact/${contactId}`)
    .then(({ data }) => {
      dispatch(updateSelectedContact(data));
      resolve(data);
    })
    .catch(reject)
    .finally(() => {
      loading(false);
    });
});
