import { actionCreator } from '..';
import { contact } from '../../types/contact';
import { UPDATE_CONTACT_LIST_DATA, UPDATE_CONTACT_LOADING_STATE, UPDATE_SELECTED_CONTACT } from '../actionTypes/contact';

/**
 * update contact loading state
 * @param {Object} payload
 * @param {Array<contact>} payload.data
 * @param {number} payload.total
 * @returns {actionCreator}
 */
export const updateContacts: actionCreator<{
    data: contact[];
    total: number;
}> = (payload) => ({
  type: UPDATE_CONTACT_LIST_DATA,
  payload
});

/**
 * update contact loading state
 * @param {boolean} payload
 * @returns {actionCreator}
 */
export const updateContactLoadingState: actionCreator<boolean> = (payload) => ({
  type: UPDATE_CONTACT_LOADING_STATE,
  payload
});

/**
 * update selected contact data
 * @param {contact} payload
 * @returns {actionCreator}
 */
export const updateSelectedContact: actionCreator<contact> = (payload) => ({
  type: UPDATE_SELECTED_CONTACT,
  payload
});
